.muzieknootjes {
  display: block;
  margin: auto;
  position: relative;
  width: 30%;
  min-width: 300px;
  height: 80px;
  border: 0px solid #000;
}

.noot-1,
.noot-2,
.noot-3,
.noot-4 {
  position: absolute;
  -webkit-animation: notes 2s infinite linear;
          animation: notes 2s infinite linear;
  font-size: 25px;
  opacity: 0;
}

.noot-1 {
  top: 60px;
  left: 0;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.noot-2 {
  top: 30px;
  left: 30%;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.noot-3 {
  top: 90px;
  left: 60%;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}

.noot-4 {
  top: 40px;
  left: 90%;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

@-webkit-keyframes notes {
  0% {
    transform: scale(1) translate(0, 0);
    opacity: 0;
  }
  50% {
    opacity: 1;
    transform: scale(1.5) translate(50%, -50%);
  }
  80% {
    opacity: 0;
    transform: scale(1.5) translate(100%, -100%);
  }
  100% {
    transform: scale(1.5) translate(100%, -100%);
    opacity: 0;
  }
}

@keyframes notes {
  0% {
    transform: scale(1) translate(0, 0);
    opacity: 0;
  }
  50% {
    opacity: 1;
    transform: scale(1.5) translate(50%, -50%);
  }
  80% {
    opacity: 0;
    transform: scale(1.5) translate(100%, -100%);
  }
  100% {
    transform: scale(1.5) translate(100%, -100%);
    opacity: 0;
  }
}

